/*################################*\
    legacy  |  _c-Hero.less
\*################################*/
.c-Hero {
  margin-bottom: 3rem;
  margin-bottom: calc(2 * var(--lh));
  margin-bottom: 2lh;
  background-color: var(--color-primary, #6a9556);
  color: #fff;
}
@supports (margin-block-end: var(--v)) {
  .c-Hero {
    margin-bottom: unset;
    margin-block-end: calc(2 * var(--lh));
    margin-block-end: 2lh;
  }
}
.c-Hero__Img {
  display: block;
  width: 100%;
}
@supports (inline-size: 1%) {
  .c-Hero__Img {
    width: unset;
    inline-size: 100%;
  }
}
.c-Hero__Hn,
.c-Hero__Blurb {
  position: relative;
  padding-left: 2rem;
  padding-right: 2rem;
}
@suports (padding-inline: 1rem) {
  padding-left: unset;
  padding-right: unset;
  padding-inline: 2rem;
}
.c-Hero__Hn {
  font-family: 'futura-pt', 'Open Sans', 'Helvetica Neue', 'Helvetica', sans-serif;
  color: inherit;
  color: unset;
  font-size: calc(var(--font-scale, 1) * 1em);
  line-height: calc(var(--line-height) / var(--font-scale, 1) * var(--tracks, 1));
  --font-scale: 1.25;
  --tracks: 1;
}
@media screen and (min-width: 45em) {
  .c-Hero__Hn {
    --font-scale: 1.75;
    --tracks: 2;
  }
}
@media screen and (min-width: 60em) {
  .c-Hero__Hn {
    --font-scale: 2.25;
  }
}
@media (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .c-Hero__Hn {
    font-size: 1.25em;
    line-height: 1.2;
  }
}
@media (-ms-high-contrast: none) and (min-width: 45em), (-ms-high-contrast: active) and (min-width: 45em) {
  .c-Hero__Hn {
    font-size: 1.75em;
    line-height: 1.71428571;
  }
}
@media (-ms-high-contrast: none) and (min-width: 60em), (-ms-high-contrast: active) and (min-width: 60em) {
  .c-Hero__Hn {
    font-size: 2.25em;
    line-height: 1.33333333;
  }
}
.c-Hero__Blurb {
  padding-bottom: 1.5rem;
  padding-bottom: var(--lh);
  padding-bottom: 1lh;
  font-weight: 300;
}
@supports (padding-block-end: var(--v)) {
  .c-Hero__Blurb {
    padding-bottom: unset;
    padding-block-end: var(--lh);
    padding-block-end: 1lh;
  }
}
.c-Hero__Blurb > :last-child {
  margin-bottom: 0;
  margin-block-end: unset;
}
@supports (background-image: linear-gradient(var(--dir), var(--clr), transparent)) {
  .c-Hero__Hn::before,
  .c-Hero__Blurb::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    inset-inline-start: 0;
    inset-inline-end: 0;
    inset-inline: 0;
    background-image: linear-gradient(var(--direction), var(--color-primary, #6a9556), transparent);
  }
  .c-Hero__Hn::before {
    --direction: to top;
    top: calc(-2 * var(--lh));
    top: -2lh;
    height: calc(2 * var(--lh));
    height: 2lh;
  }
  @supports (inset-block-start: var(--v)) and (block-size: var(--v)) {
    .c-Hero__Hn::before {
      top: unset;
      height: unset;
      inset-block-start: calc(-2 * var(--lh));
      inset-block-start: -2lh;
      block-size: calc(2 * var(--lh));
      block-size: 2lh;
    }
  }
  .c-Hero__Blurb::after {
    --direction: to bottom;
    bottom: calc(-1 * var(--lh));
    bottom: -1lh;
    height: calc(1 * var(--lh));
    height: 1lh;
  }
  @supports (inset-block-end: var(--v)) and (block-size: var(--v)) {
    .c-Hero__Blurb::after {
      bottom: unset;
      height: unset;
      inset-block-end: calc(-1 * var(--lh));
      inset-block-end: -1lh;
      block-size: calc(1 * var(--lh));
      block-size: 1lh;
    }
  }
}
